export default {
	data() {
		return {
			key:'',
			secret:'',
			goodsList:[],
			loadingText:"正在加载中...",
			headerTop:"0px",
			headerPosition:"fixed",
			orderbyList:[
				{text:"销量",selected:true,orderbyicon:false,orderby:0},
				{text:"价格",selected:false,orderbyicon:['sheng','jiang'],orderby:0},
				{text:"好评",selected:false,orderbyicon:false,orderby:0}
			],
			orderby:"sheng",
			pageCount:1,
			pageid:1,
			scrollTop:0
		};
	},
	onLoad: function () { 
		this.getgoodList()
		//兼容H5下排序栏位置










	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(()=>{
			this.reload();
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom(){			
		
	},
	methods:{
		loadMore(){
			this.pageid++;
			console.log(this.pageid)
			if(this.pageid>this.pageCount){
				this.loadingText="到底了";
				return false;
			}else{
				this.getgoodList();
				this.loadingText="正在加载...";
			}
		},
		// 获取商品
		getgoodList() {
			let data = {
				big: 1,
				mid: '',
				small: '',
				pageid: this.pageid
			}
			this.$xm.post('/Product/proList', data, (res) => {
				let result = res.prolist;
				if (res.prolist.length == 0) {
					this.loadingText = '没有更多商品！'
				} else {
					this.loadingText = '正在加载中...'
				}
				result.map((ele) => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
					ele.itemcount = 0;
		
				})
				this.pageCount=res.count;
				//接收数据，保证每次都拼接上
				this.goodsList = this.goodsList.concat(result);
			})
		
		},
		
		
		
	}
	
}
